import React, { FC, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { History, LocationState } from 'history';
import Slide from '@components/2page/ko/Slide/Slide';
import EnSlide from '@components/2page/en/Slide/EnSlide';
import './Newcategory.css';

interface Props {
  selectedcity: any;
  history: History<LocationState>;
}

const Newcategory: FC<Props> = (props: Props) => {
  var local = localStorage.getItem('language');
  var language = local.split('"');

  const [select, setSelect] = useState('');

  const onClickTOR = () => {
    setSelect('');
  };
  const onClickRES = () => {
    setSelect('');
  };
  const onClickCAF = () => {
    setSelect('');
  };

  if (language[1] === 'KO') {
    return (
      <>
        <div className="displaycourse">
          <div className="btncategory">
            <button className={select === '' ? 'newcategory_clicked' : 'newcategory'} onClick={onClickTOR}>
              <img
                className="categortimg"
                src="src/icon/.png"
                style={{ width: '3em', display: 'block', margin: 'auto' }}
              />
              
            </button>
            <button className={select === '' ? 'newcategory_clicked' : 'newcategory'} onClick={onClickRES}>
              <img
                className="categortimg"
                src="src/icon/.png"
                style={{ width: '3em', display: 'block', margin: 'auto' }}
              />
              
            </button>
            <button className={select === '' ? 'newcategory_clicked' : 'newcategory'} onClick={onClickCAF}>
              <img
                className="categortimg"
                src="src/icon/.png"
                style={{ width: '3em', display: 'block', margin: 'auto' }}
              />
              
            </button>
          </div>
        </div>
        {select === '' ? (
          <Slide selectedcity={props.selectedcity} selectedcategory={select} history={props.history} />
        ) : null}
        {select === '' ? (
          <Slide selectedcity={props.selectedcity} selectedcategory={select} history={props.history} />
        ) : null}
        {select === '' ? (
          <Slide selectedcity={props.selectedcity} selectedcategory={select} history={props.history} />
        ) : null}
      </>
    );
  } else {
    // 
    return (
      <>
        <div className="displaycourse">
          <div className="btncategory">
            <button className={select === '' ? 'newcategory_clicked' : 'newcategory'} onClick={onClickTOR}>
              <img
                className="categortimg"
                src="src/icon/.png"
                style={{ width: '3em', display: 'block', margin: 'auto' }}
              />
              ATTRACTION
            </button>
            <button className={select === '' ? 'newcategory_clicked' : 'newcategory'} onClick={onClickRES}>
              <img
                className="categortimg"
                src="src/icon/.png"
                style={{ width: '3em', display: 'block', margin: 'auto' }}
              />
              RESTAURANT
            </button>
            <button className={select === '' ? 'newcategory_clicked' : 'newcategory'} onClick={onClickCAF}>
              <img
                className="categortimg"
                src="src/icon/.png"
                style={{ width: '3em', display: 'block', margin: 'auto' }}
              />
              CAFE
            </button>
          </div>
        </div>
        {select === '' ? (
          <EnSlide selectedcity={props.selectedcity} selectedcategory={select} history={props.history} />
        ) : null}
        {select === '' ? (
          <EnSlide selectedcity={props.selectedcity} selectedcategory={select} history={props.history} />
        ) : null}
        {select === '' ? (
          <EnSlide selectedcity={props.selectedcity} selectedcategory={select} history={props.history} />
        ) : null}
      </>
    );
  }
};

export default Newcategory;
